@mixin Flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

@keyframes puls{
    0%{
        opacity: .8
    }
    50%{
        opacity: .5
    }
    100%{
        opacity: .8
    }
}

@keyframes clouds {
    from {
      background-position: 0 50%;
    }
    to {
        background-position: 100% 50%;
    }
}